import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import ReceipeCard, { ReceipeSkeletonCard } from "../ReceipeCard";
import { update } from "lodash";

const setReceipes = (payload) => {
  const data = JSON.stringify(payload);
  localStorage.setItem("receipes", data);
};

const getReceipes = () => {
  console.log("Getting from Local Storage");
  const payload = localStorage.getItem("receipes");
  const data = payload ? JSON.parse(payload) : [];
  return data;
};

const getReceipe = (id) => {
  const receipesList = getReceipes();
  const receipeData = receipesList.find((x) => x.id == id);
  return receipeData;
};

const Receipe = () => {
  const { id } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const [data, setData] = useState({});

  useEffect(() => {
    const payloadData = getReceipe(id);
    setData(payloadData);
    setTimeout(() => {
      setIsLoading(false);
    }, 500);
  }, []);

  const updateLike = (id) => {
    try {
      const data = getReceipes();
      let currentReceipe = {};
      const newData = data.map((x) => {
        if (x.id === id) {
          const liked = x.liked ? false : true;
          const newDataReceipe = { ...x, liked };
          currentReceipe = newDataReceipe;
          return newDataReceipe;
        }
        return x;
      });
      setData(currentReceipe);
      setReceipes(newData);
    } catch (err) {
      console.log("updateLikeErr", err);
    }
  };

  if (isLoading) {
    return <ReceipeSkeletonCard fullWidth={true} />;
  }
  return <ReceipeCard data={data} fullWidth={true} updateLike={updateLike} />;
};

export default Receipe;
