import React, { useState, useEffect } from "react";
import Axios from "axios";
import styles from "./index.module.css";
import Grid from "@material-ui/core/Grid";
import Search from "@material-ui/icons/Search";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import ReceipeCard, { ReceipeSkeleton } from "../ReceipeCard";

const setReceipes = (payload) => {
  const data = JSON.stringify(payload);
  localStorage.setItem("receipes", data);
};

const getReceipes = () => {
  console.log("Getting from Local Storage");
  const payload = localStorage.getItem("receipes");
  const data = payload ? JSON.parse(payload) : [];
  return data;
};

const getReceipesFromAPI = async () => {
  console.log("Getting from API");
  const response = await Axios.get(`https://starlord.hackerearth.com/recipe`);
  const posts = response.data;
  return posts;
};

const SearchBox = ({ onChange }) => {
  return (
    <div className={styles.searchBox}>
      <Grid container spacing={1} alignItems="flex-end">
        <Grid item>
          <Search />
        </Grid>
        <Grid item>
          <TextField
            id="input-with-icon-grid"
            label="Search for Recipes"
            onChange={onChange}
            fullWidth={true}
          />
        </Grid>
      </Grid>
    </div>
  );
};

const DialogModal = ({ data = {}, isOpen, handleClose, updateLike }) => {
  return (
    <Dialog
      open={isOpen}
      keepMounted
      onClose={handleClose}
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <ReceipeCard
        data={data}
        fullWidth={true}
        updateLike={() => updateLike(data.id)}
      />
      <DialogContent />
    </Dialog>
  );
};

const Receipes = () => {
  const [data, setData] = useState([]);
  const [currentReceipe, setCurrentReceipe] = useState({});
  const [isLoading, setisLoading] = useState(true);
  const [isOpen, setIsOpen] = useState(false);
  const [searchText, setSearchText] = useState("");

  const initData = async () => {
    const isCached = getReceipes();
    const payload =
      isCached && isCached.length > 0 ? isCached : await getReceipesFromAPI();
    setReceipes(payload);
    setData(payload);

    setTimeout(() => {
      setisLoading(false);
    }, 500);
  };

  useEffect(() => {
    initData();
  }, []);

  const onChange = (e) => {
    const searchText = e.target.value;
    setSearchText(searchText);
    console.log(searchText);
    if (searchText.length > 0) {
      const newData = data.filter((x) =>
        x.name.toLowerCase().includes(searchText.toLowerCase())
      );
      setData(newData);
    } else {
      const newData = getReceipes();
      setData(newData);
    }
  };

  const ReceipeList = ({ data }) => {
    return (
      <div className={styles.receipeListContainer}>
        {data && data.length > 0 ? (
          data.map((item) => (
            <ReceipeCard
              key={item.id}
              data={item}
              onClickModal={() => openModal(item)}
              updateLike={updateLike}
            />
          ))
        ) : (
          <div>Not Found</div>
        )}
      </div>
    );
  };

  const updateLike = (id) => {
    try {
      const data = getReceipes();
      let currentReceipe = {};
      const newData = data.map((x) => {
        if (x.id === id) {
          const liked = x.liked ? false : true;
          const newDataReceipe = { ...x, liked };
          currentReceipe = newDataReceipe;
          return newDataReceipe;
        }
        return x;
      });
      setData(newData);
      setReceipes(newData);
      setCurrentReceipe(currentReceipe);
    } catch (err) {
      console.log("updateLikeErr", err);
    }
  };

  const openModal = (item) => {
    setCurrentReceipe(item);
    setIsOpen(true);
  };

  const onModalClose = () => {
    setIsOpen(false);
  };

  return (
    <div className={styles.container}>
      <SearchBox onChange={onChange} />
      {isLoading ? <ReceipeSkeleton /> : <ReceipeList data={data} />}

      <DialogModal
        data={currentReceipe}
        isOpen={isOpen}
        handleClose={onModalClose}
        updateLike={updateLike}
      />
    </div>
  );
};

export default Receipes;
