import React, { useState, useEffect } from "react";
import styles from "./Home.module.css";
import Receipe from "../../modules/Recipe";
import Receipes from "../../modules/Recipes";
import { BrowserRouter, Route, Switch, Redirect, Link } from "react-router-dom";

function Home() {
  return (
    <div>
      <div className={styles.container}>
        <h1 className={styles.title}>Recipes Ready!</h1>
        <BrowserRouter>
          <Switch key={location.pathname}>
            <Route path="/" exact component={Receipes} />
            <Route path="/:id" component={Receipe} />
          </Switch>
        </BrowserRouter>
      </div>
    </div>
  );
}

function NetworkChecker() {
  const [isOnline, setNetwork] = useState(window.navigator.onLine);
  const updateNetwork = () => {
    setNetwork(window.navigator.onLine);
  };
  useEffect(() => {
    window.addEventListener("offline", updateNetwork);
    window.addEventListener("online", updateNetwork);
    return () => {
      window.removeEventListener("offline", updateNetwork);
      window.removeEventListener("online", updateNetwork);
    };
  });
  return (
    <div className={styles.networkChecker}>
      Network Status :{" "}
      {isOnline ? (
        <span className={[styles.networkChipOnline]}>Online</span>
      ) : (
        <span className={[styles.networkChipOffline]}>Offline</span>
      )}
    </div>
  );
}

export default Home;
