import React from "react";
import styles from "./index.module.css";
import AttachMoney from "@material-ui/icons/AttachMoney";
import FavoriteIcon from "@material-ui/icons/Favorite";
import FavoriteBorder from "@material-ui/icons/FavoriteBorder";
import Bookmark from "@material-ui/icons/Bookmark";

import { useHistory } from "react-router-dom";
import Skeleton from "@material-ui/lab/Skeleton";

const ReceipeCard = ({ data, fullWidth, onClickModal, updateLike }) => {
  const history = useHistory();
  const redirectTo = () => {
    history.push(`/${data.id}`);
  };

  const LikeBox = ({ liked }) => {
    if (liked) {
      return <FavoriteIcon onClick={() => updateLike && updateLike(data.id)} />;
    } else {
      return (
        <FavoriteBorder onClick={() => updateLike && updateLike(data.id)} />
      );
    }
  };

  return (
    <div
      className={
        styles.receipeCardBox +
        (fullWidth ? ` ${styles.receipeCardBoxFullWidth}` : "") +
        " "
      }
    >
      <div className={styles.receipeCard}>
        <div className={styles.imgWrapper} onClick={redirectTo}>
          <div className={styles.categoryBox}>{data.label}</div>
          <img src={data.image} />
          <div className={styles.overlay} />
        </div>
        <div className={styles.receipeCardBody}>
          <div className={styles.receipeCardBodyTitleContainer}>
            <div onClick={redirectTo}>
              <h2>{data.name}</h2>
              <span className={styles.categoryText}>
                <Bookmark fontSize={"small"} />
                {data.category}
              </span>
              <p className={styles.priceBox}>
                <AttachMoney fontSize={"small"} /> {data.price}
              </p>
            </div>
            <LikeBox liked={data.liked} />
          </div>

          <p>{data.description}</p>
        </div>

        {!fullWidth ? (
          <div
            className={styles.modalBox}
            onClick={() => onClickModal && onClickModal(data)}
          >
            <p>Open on Modal</p>
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default ReceipeCard;

export const ReceipeSkeleton = () => {
  return (
    <div className={styles.receipeListContainer}>
      {[1, 2, 3, 4, 5, 6].map((x) => (
        <ReceipeSkeletonCard />
      ))}
    </div>
  );
};

export const ReceipeSkeletonCard = ({ fullWidth }) => (
  <div
    className={
      styles.receipeCardBox +
      (fullWidth ? ` ${styles.receipeCardBoxFullWidth}` : "")
    }
  >
    <div className={styles.receipeSkeletonCard}>
      <Skeleton
        animation="wave"
        variant="rect"
        className={styles.skeletonCard}
        height={"450px"}
      />
    </div>
  </div>
);
